function ScreenEight({ next }) {
  const getEnter = (e) => {
    const t = {
      opacity: 0,
      scale: 0.8,
      y: '-100%',
    };
    if (e.index >= 2 && e.index <= 6) {
      return { ...t, y: '-30%', duration: 150 };
    }
    return t;
  }
  const geInterval = (e) => {
    switch (e.index) {
      case 0:
        return 0;
      case 1:
        return 150;
      case 2:
      case 3:
      case 4:
      case 5:
      case 6:
        return 150 + 450 + (e.index - 2) * 10;
      default:
        return 150 + 450 + (e.index - 6) * 150;
    }
  }
  setTimeout(() => {
    next()
  }, 5000);
  return (
    <div className="screenTwo">
      <Texty
        type="mask-top"
        delay={400}
        enter={getEnter}
        interval={geInterval}
        component={TweenOne}
        componentProps={{
          animation: [
            { x: 130, type: 'set' },
            { x: 100, delay: 500, duration: 450 },
            {
              ease: 'easeOutQuart',
              duration: 300,
              x: 0,
            },
            {
              letterSpacing: 0,
              delay: -300,
              scale: 0.9,
              ease: 'easeInOutQuint',
              duration: 1000,
            },
            { scale: 1, width: '100%', delay: -300, duration: 1000, ease: 'easeInOutQuint' },
          ],
        }}
      >
        嫁给我，好吗？就让这份爱延续下去，forever!
      </Texty>
    </div>
  )
}

function screenTen() {
  const p0 = 'M0,100 L25,100 C34,20 40,0 100,0';
  const p1 = 'M0,100 C5,120 25,130 25,100 C30,60 40,75 58,90 C69,98.5 83,99.5 100,100';
  const ease0 = TweenOne.easing.path(p0);
  const ease1 = TweenOne.easing.path(p1);
  const animation = [
    {
      repeatDelay: 300,
      y: -70,
      repeat: -1,
      yoyo: true,
      ease: ease0,
      duration: 300
    },
    {
      repeatDelay: 300,
      appearTo: 0,
      scaleX: 0,
      scaleY: 2,
      repeat: -1,
      yoyo: true,
      ease: ease1,
      duration: 300,
    }
  ];
  return (
    <TweenOne
      animation={animation}
      className="code-box-shape"
    >
      嫁给我！嫁给我！
    </TweenOne>
  )
}

function App() {
  const components = [ScreenTwo, ScreenThree, ScreenFour, ScreenFive, ScreenSix, ScreenSeven, ScreenEight, screenTen]
  let [i, setIndex] = useState(0)
  const next = useCallback(() => {
    setIndex(i + 1)
  }, [i])
  return (
    <div id="App">
      <div className="center">
        {
          components.map((C, index) => {
            return (
              <React.Fragment key={index}>
                {
                  i === index && <C next={next} />
                }
              </React.Fragment>
            )
          })
        }
        {
          i === 8  && <ScreenBg />
        }
      </div>
    </div>
  );
}

export default App;
